























	import xHeader from "../../components/xHeader.vue";
	import comm from '../../static/js/common.js';
	export default {
		data() {
			return {
				titleh: '修改密码',
				iconh: '&#xe600;',
				tips: '',
				newPassword: '',
				oldPassword: ''
			}
		},
		components: {
            xHeader
        },
		onNavigationBarButtonTap(e) {
			this.tips = e
		},
		onLoad() {

		},
		methods: {
			formSubmit: function() {
				let that = this;
				if(that.verification()) {
					let param = {
						oldPassword: that.oldPassword,
						newPassword: that.newPassword
					}
					comm.post(comm.host_gp + "/api/f/user/resetPassword", param).then(function (res) {
						res = res.data;
						if(res.code == 0) {
							uni.showToast({
								title: '修改成功',
								icon: 'none',
								duration: 2000
							})
							setTimeout(function() {
								uni.navigateBack()
							}, 1000)
						}else {
							uni.showToast({
								title: res.msg,
								icon: 'none',
								duration: 2000
							})
						}
					})
				};
			},
			formReset: function(e) {
				let that = this;
				that.oldPassword = '';
				that.newPassword = '';
			},
			verification: function() {
				let that = this;
				if(that.oldPassword == '') {
					uni.showToast({
						title: '旧密码不能为空',
						icon: 'none',
						duration: 2000
					});
					return false
				}
				if(that.newPassword == '') {
					uni.showToast({
						title: '新密码不能为空',
						icon: 'none',
						duration: 2000
					});
					return false
				}
				return true;
			}
		}
	}
